import React from 'react'
import classes from './NavItem.module.css'
import {NavLink} from 'react-router-dom'
const NavItem =(props)=>{
  return (
    <li className={classes.navli}><NavLink className={classes.navitem} to={props.link}>{props.children}</NavLink> </li>
  )
}


export default NavItem

